import React, { useState } from 'react'
import EmailSupportModal from './EmailSupportModal';

export default function Footer(props) {
    const [isOpen, setIsOpen] = useState(false);

    return (
        <>
        {!props?.login ?
            <footer className="bg-white mt-2 flex justify-center items-center shadow-md px-3 md:px-5 py-4 lg:px-10 w-[100%]">
            <div className="text-sm text-center text-black">Need support ? Click <a className='text-primary cursor-pointer font-semibold' onClick={()=>setIsOpen(!isOpen)}>here</a></div>
            <EmailSupportModal isOpen={isOpen} setIsOpen={setIsOpen}/>
        </footer>:null
        }
        </>
    )
}