import { Form, Link } from '@remix-run/react';
import React, { useEffect, useState } from 'react'
import Modal from 'react-modal';
import { toast } from "react-toastify";
import closeIcon from "~/assets/images/closeIcon.png"
import { MAIL_SENDER, SEND_SUPPORT_MAIL, SUPPORT_MAIL_SENDER_LIST } from "~/utils/constants.js";

const MAX_FILE_SIZE = 2000000

//Modal for Station banner preview and menu of the day
export const WelcomeEmail = ({ firstName }) => ({
    subject: `👋 ${firstName}`,
    body: (
        <div>
            <p>Hello {firstName}!</p>
            <p>Hope you'll enjoy the package!</p>
        </div>
    )
})
export default function EmailSupportModal({ isOpen, setIsOpen }) {
    const [email, setEmail] = useState("")
    const [name, setName] = useState("")
    const [title, setTitle] = useState("")
    const [message, setMessage] = useState("")
    const [attachment, setAttachment] = useState()
    const [file, setFile] = useState("")
    const buttonRef=React.useRef(null);
    const [preventDouble,setPreventDouble]=useState(false)

    const closeModal = () => {
        setName("")
        setEmail("")
        setTitle("")
        setMessage("")
        setAttachment("")
        setIsOpen(false);
    }
    // const customStyles = {
    //     content: {
    //         top: '50%',
    //         left: '50%',
    //         right: 'auto',
    //         bottom: 'auto',
    //         marginRight: '-50%',
    //         transform: 'translate(-50%, -50%)',
    //         padding: '0px',
    //         width: "600px"
    //     },
    //     overlay: {

    //     }
    // };
    const readFileAsync = async (file) => {
        return new Promise((resolve, reject) => {
            var filereader = new FileReader();
            filereader.readAsDataURL(file);
            var base64
            filereader.onload = () => {
                base64 = resolve(filereader.result);
            };
            filereader.onerror = reject;
            setFile(base64)
            return base64
        })
    }
    const ShowToast=(errMessage)=>{
         toast.error(errMessage, {
      onClose: () => setPreventDouble(false)   
    });
    }

    const EmailSupport = async () => {  
        setPreventDouble(true)                                                                                                                                                                                                                                             
        if (!name) {
            ShowToast("Name is required field");
        
        } else if (!email) {
            ShowToast("Email is required field");
        }
        else if (!(/^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/.test(email))) {
            ShowToast("Enter a valid email address");
        }
        else if (!message) {
            ShowToast("Message is required field");

        }
        else if(attachment?.target?.files[0].name.length > 0 && !(/.*\.(?:jpg|png|pdf|doc|docx)$/.test(attachment?.target?.files[0].name))){
            ShowToast("Upload a valid file format");

        }
        else if (attachment?.target?.files[0].size > MAX_FILE_SIZE) {
            ShowToast("Please upload file less than 2MB.");
        }
        else {
            var files = attachment?.target?.files ? await readFileAsync(attachment?.target?.files[0]) : ""
            var msg = {
                "from": MAIL_SENDER,
                "to": SUPPORT_MAIL_SENDER_LIST,
                "subject": 'Support Email',
                "html": `
                    <div>
                        <p>Hi</p>
                        <p><strong>Name:</strong>${" "}${name}.</p>
                        <p><strong>Email:</strong>${" "}${email}.</p>
                        <p><strong>Title: </strong>${" "}${title}.</p>
                        <p><strong>Message: </strong>${" "}${message}.</p>
                    <div>
                `,
                attachments: files ? ([
                    {   // data uri as an attachment  
                        filename: attachment?.target?.files[0]?.name,
                        path: files
                    }
                ]):[],
            };
            await fetch(SEND_SUPPORT_MAIL, {
                method: 'POST',
                mode: 'cors',
                cache: 'no-cache',
                credentials: 'same-origin',
                headers: {
                    'Content-Type': 'application/json'
                },
                redirect: 'follow',
                referrerPolicy: 'no-referrer',
                body: JSON.stringify(msg)
            }).then(e =>{
                
                toast.success("Support form submitted successfully",{
                    onClose: () => setPreventDouble(false)  
                });
                closeModal();
             
            }
            ).catch(e =>{
                console.log("error",e)
                ShowToast("Something went wrong");
        })
        

        }

    }
    return (
        <div className='w-100,h-100'>
            <Modal
                isOpen={isOpen}
                onRequestClose={closeModal}
                ariaHideApp={false}
                contentLabel="Banner Preview"
                className={"support-form"}
            >
                <div className="bg-[#FAFAFA] rounded-[10px] shadow-lg px-6 py-12 relative lg:px-9 station-preview">
                    <div className="close-div"><button onClick={closeModal} className="float-right"><img src={closeIcon} alt="close-icon" /> </button></div>
                    <div className="flex justify-center items-center px-5 py-[30px]">
                        <div
                            className="max-w-[400px] w-full flex flex-col items-center text-[14px] "
                        >
                            <h1 className="text-center text-[24px] font-bold text-primary mb-5">
                                Support Form
                            </h1>
                            <div className="mb-2 w-full">
                                <label
                                    htmlFor="u-mail-id1"
                                    className="block text-sm font-bold text-primary"
                                >
                                    Name
                                </label>
                                <input
                                    type="text"
                                    id="u-name-id1"
                                    name="name"
                                    placeholder="Name"
                                    value={name}
                                    onChange={(e) => setName(e.target.value)}
                                    required
                                    className="mt-2 w-full px-4 py-3 flex justify-center items-center text-black shadow-sm border border-primary focus:border-primary placeholder:font-bold placeholder:text-[#6E7878] placeholder:text-[14px] focus:outline-none focus:ring-1 focus:ring-primary sm:text-sm"
                                />
                            </div>
                            <div className="mb-2 w-full">
                                <label
                                    htmlFor="u-mail-id1"
                                    className="block text-sm font-bold text-primary"
                                >
                                    Email
                                </label>
                                <input
                                    type="email"
                                    id="u-mail-id1"
                                    name="email"
                                    placeholder="Your@email.dk"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}
                                    required
                                    className="mt-2 w-full px-4 py-3 flex justify-center items-center text-black shadow-sm border border-primary focus:border-primary placeholder:font-bold placeholder:text-[#6E7878] placeholder:text-[14px] focus:outline-none focus:ring-1 focus:ring-primary sm:text-sm"
                                />
                            </div>
                            <div className="mb-2 w-full">
                                <label
                                    htmlFor="u-mail-id1"
                                    className="block text-sm font-bold text-primary"
                                >
                                    Title
                                </label>
                                <input
                                    type="text"
                                    id="u-title-id1"
                                    name="title"
                                    placeholder="Share the title"
                                    value={title}
                                    onChange={(e) => setTitle(e.target.value)}
                                    // required
                                    className="mt-2 w-full px-4 py-3 flex justify-center items-center text-black shadow-sm border border-primary focus:border-primary placeholder:font-bold placeholder:text-[#6E7878] placeholder:text-[14px] focus:outline-none focus:ring-1 focus:ring-primary sm:text-sm"
                                />
                            </div>
                            <div className="mb-2 w-full">
                                <label
                                    htmlFor="u-mail-id1"
                                    className="block text-sm font-bold text-primary"
                                >
                                    Message
                                </label>
                                <textarea
                                    type="text"
                                    id="u-title-id1"
                                    name="title"
                                    placeholder="Share the message"
                                    value={message}
                                    onChange={(e) => setMessage(e.target.value)}
                                    required
                                    className="mt-2 w-full px-4 py-3 flex justify-center items-center text-black shadow-sm border border-primary focus:border-primary placeholder:font-bold placeholder:text-[#6E7878] placeholder:text-[14px] focus:outline-none focus:ring-1 focus:ring-primary sm:text-sm"
                                />
                            </div>
                            <div className="mb-2 w-full">
                                <label
                                    htmlFor="u-mail-id1"
                                    className="block text-sm font-bold text-primary"
                                >
                                    Attachments
                                </label>
                                <input
                                    type="file"
                                    id="u-attachment-id1"
                                    name="title"
                                    placeholder="Share the title"
                                    accept=".png,.jpg,.pdf,.docx,.doc"
                                    onChange={(e) => setAttachment(e)}
                                    className="mt-2 w-full px-4 py-3 flex justify-center items-center text-black shadow-sm border border-primary focus:border-primary placeholder:font-bold placeholder:text-[#6E7878] placeholder:text-[14px] focus:outline-none focus:ring-1 focus:ring-primary sm:text-sm"
                                />
                                <span className='red-text text-xs'>*Upload file must be .png,.jpg,.pdf,.docx and .doc less than 2MB </span>
                            </div>
                            <div className="mt-6 w-full">
                                <button
                                    type="submit"
                                    ref={buttonRef}
                                    disabled={preventDouble}
                                    onClick={() => preventDouble?null:EmailSupport()}
                                    className="inline-flex w-full items-center justify-center h-[50px] text-[18px] rounded border bg-primary border-primary px-4 py-2 text-sm font-bold text-white shadow-sm hover:bg-primary focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2"
                                >

                                    Submit
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal>
        </div>
    )
}